<template>
  <div class="tmpl">
    <nav-bar title="登录"></nav-bar>
    <div class="login">
      <mt-field label="账号" placeholder="用户名/手机号/邮箱" v-model="username"></mt-field>
      <mt-field label="密码" placeholder="密码" type="password" v-model="password"></mt-field>
      <mt-switch v-model="isSwitch" style="padding: 0 10px;">自动登陆</mt-switch>
      <div class="login-btn">
        <mt-button type="primary" size="large" @click="login()">登陆</mt-button>
        <div class="link-area"><router-link :to="{name:'register'}">注册账号</router-link> <span class="spliter">|</span> <a>忘记密码</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import Cookie from '../common/cookie.js'
  export default {
    name: "Login",
    data(){
        return{
          isSwitch:true,//是否自动登陆
          username:'',
          password:'',
        }
      },
    methods:{
        login(){
          if(this.username.length == 0){
            Toast("请填写用户名/手机号/邮箱");
            return;
          }
          if(this.password.length == 0){
            Toast("请填写密码");
            return;
          }
          this.$axios.post(this.$host+"api/login/","username="+this.username+"&password="+this.password)
            .then(res=>{
              if(res.data.code == 200) {
                //console.log(res.data.data.username);
                Cookie.setCookie("username",res.data.data.username,7);
                Cookie.setCookie("token",res.data.data.token,7);
                if(res.data.data.face){
                  Cookie.setCookie("face",res.data.data.face,7);
                }
                Toast(res.data.msg);
                this.$router.push({
                  name:'member',
                });
              }else{
                //console.log(res.data);
                Toast(res.data.msg);
                return;
              }
            })
        }
      }
  }
</script>

<style scoped>
  .login{
    padding-top: 20px;
  }
  .login-btn{
    padding: 0 10px;
    margin-top: 25px;
  }
  .link-area {
    display: block;
    margin-top: 25px;
    text-align: center;
  }

  .spliter {
    color: #bbb;
    padding: 0px 8px;
  }

</style>



